#collect > .collectList .collectItem {
  width: 100%;
  height: 2.5rem;
  padding: .2rem;
  overflow: hidden;
  background: white;
  display: flex; }
  #collect > .collectList .collectItem .collectImg {
    width: auto;
    height: 100%; }
  #collect > .collectList .collectItem .collectMsg {
    flex: 1 1; }
    #collect > .collectList .collectItem .collectMsg > div {
      height: 33.333%;
      display: flex;
      align-items: center;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis; }
    #collect > .collectList .collectItem .collectMsg > div:nth-child(1) {
      font-size: .32rem; }
    #collect > .collectList .collectItem .collectMsg > div:nth-child(2) {
      color: darkgrey; }
    #collect > .collectList .collectItem .collectMsg > div:nth-child(3) {
      color: red; }

.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    background: white;
    box-shadow: 0 .04rem .08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}
.header>div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .left,.header .right{
    width: .6rem;
}
.header .title{
    flex: 1 1;
    font-size: .32rem;
}
.touchClear {
  height: auto;
  width: 100%;
  position: relative; }
  .touchClear .test {
    position: relative;
    height: auto;
    background: #19d5fd;
    transition: all .2s linear;
    z-index: 2;
    text-align: center; }
  .touchClear .test.left {
    transform: translateX(-60px); }
  .touchClear .test.default {
    transform: translateX(0); }
  .touchClear .colse {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 100%;
    background: red;
    z-index: 1;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .36rem; }

